<template>
	<div id="list" name="list">
		<dl>
      <dt><span></span></dt>
      <dd class="choosed"><span><img class="icon" src="../assets/music.png"> </span></dd>  
      <dd><span><img class="icon" src="../assets/fm.png">FM</span></dd>  
      <dd><span><img class="icon" src="../assets/mv.png">MV</span></dd>  
      <dd><span><img class="icon" src="../assets/friend.png"></span></dd>  
      <dt><span></span></dt>
      <dd><span><img class="icon" src="../assets/local_music.png"></span></dd>  
      <dd><span><img class="icon" src="../assets/download.png"></span></dd>  
      <dd><span><img class="icon" src="../assets/cloud.png"></span></dd>  
      <dd><span><img class="icon" src="../assets/singer.png"></span></dd>  
      <dd><span><img class="icon" src="../assets/station.png"></span></dd>
      <dt><span></span></dt>
      <dd><span><img class="icon" src="../assets/heart.png"></span></dd>  
      <dd><span><img class="icon" src="../assets/list.png">light</span></dd>  
      <dd><span><img class="icon" src="../assets/list.png"></span></dd>  
      <dd><span><img class="icon" src="../assets/list.png"></span></dd>  
      <dt><span></span></dt>
      <dd><span><img class="icon" src="../assets/list.png"></span></dd> 
      <dd><span><img class="icon" src="../assets/list.png"></span></dd> 
      <dd><span><img class="icon" src="../assets/list.png">loli</span></dd> 
      <dd><span><img class="icon" src="../assets/list.png"></span></dd> 
      <dd><span><img class="icon" src="../assets/list.png"></span></dd> 
      <dd><span><img class="icon" src="../assets/list.png">30</span></dd>   
      <dd><span><img class="icon" src="../assets/list.png"></span></dd> 
      <dd><span><img class="icon" src="../assets/list.png"></span></dd> 
    </dl>
	</div>
</template>
<script >
export default {
  name: 'list',
  data () {
    return {
      app: ''
    }
  }
}
</script>
<style scoped>
#list{
	height: 100%;
	width: 20%;
  background-color: rgb(245,245,247);
  float: left;
  overflow-y: scroll;
}

dt,dd{
  height: 30px;
  font-size: 15px;
  line-height: 30px;
  font-family: microsoft yahei;
  position: relative;
}

dd{
  text-indent: 20px;
  opacity: 0.5;
}

dd:hover{
  opacity: 1.0;
  cursor: pointer;
}

dt{
  text-indent: 10px;
  color: rgb(125,125,125);
}

.icon{
  width: 20px;
  height: 20px;
  vertical-align: top;
  margin-right: 10px;
  margin-top: 5px;
}

.choosed{
  background-color: rgb(230,231,234);
  opacity: 1.0;
}

.choosed:before{
  position: absolute;
  left: 0px;
  top: 0px;
  display: inline-block;
  content: ' ';
  width:5px;
  height: 30px;
  background-color: rgb(198,47,47);
}


/* */
::-webkit-scrollbar {
  width: 7px;
  background-color: #F5F5F5;
}
/* +*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: rgb(245,245,247);
}
/* +*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgb(225,225,226);
  background-color: rgb(225,225,226);
}
</style>



